html {
    scroll-behavior: smooth;
}
.AboutUs {
    width: 100%;
    max-width: 1400px;
    margin: 1em auto;
    margin-top: 0em;
    position: relative;
    
    @media screen and (max-width:500px){
        overflow: hidden;
    }
    & > h1 {
        font-size: 5em;
        text-align: center;
        margin-bottom: .2em;
        color: $mainColor;
        @media screen and (max-width:500px){
            margin-bottom: 0;
            font-size: 4em;
        }
    }
    .innerAboutUs {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        padding: 1em;
        div {
            position: relative;
            video {
                width: 100%;
                max-width: 900px;
                border-radius: 30px;
                cursor: pointer;
            }
        }
        .AboutUsTextHolder{
            width: 100%;
            max-width: 900px;
            margin-top: 2em;
            padding: 1em 2.3em;
            border-radius: 10px;
            background: rgb(37 150 190 / 11%);
            @media screen and (max-width:500px){
                margin-top: 1em;
            }
            h2{
                padding: 0;
                font-size: 1.7em;
                @media (max-width:500px) {
                    font-size: 1.5em;
                }
                color: $secondColor;
            }
            p{
                line-height: 1.9em;
                text-align: justify;
                padding: 0;
                color: $secondColor;
                text-transform: capitalize;
                
                @media (max-width:500px) {
                    font-size: .9em;
                }
            }
        }
    }
}

.playPauseBtn {
    position: absolute;
    font-size: 6em;
    font-weight: 900;
    color: $mainColor;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    cursor: pointer;
    transition: font-size .3s ease , filter .3s ease;
    
    @media screen and (max-width:500px){
        font-size: 4em;
    }
    &:hover {
        font-size: 6.3em;
        @media screen and (max-width:500px){
            font-size: 4.3em;
        }
    }
}

.backgroundText {
    position: absolute;
    top: 3em;
    display: flex;
    flex-direction: column;
    width: 100%;
    span{
        font-size: 15em;
        color: $mainColor;
        filter: blur(40px);
        width: 100%;
        margin-top: 40px;
        text-align: end;
    }
    span:nth-child(2){
        text-align: start;
    }
}


#middleTextAboutUs{
    @media screen and (max-width:500px){
        display: none;
    }
}