
.Services {
    width: 100%;
    margin-top: 2.2em;
    h1 {
        text-align: center;
        color: $mainColor;
        font-size: 3em;
    }
    position: relative;
    .innerServices{
        width: 100%;
        max-width: 1200px;
        margin: 0 auto;
        overflow-x: hidden;
        overflow-y: hidden;
        div{
            width: 100%;
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
        }
        .service{
            z-index: 99;
            width: 200px;
            background-color: $secondColor;
            height: 200px;
            margin: .5em;
            border-radius: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            flex-wrap: nowrap;
            position: relative;
            cursor: pointer;
            overflow: hidden;
            @media screen and (max-width:500px){
                width: 150px;
                height: 150px;
            }
            svg{
                font-size: 3em;
                color: white;
                transition: color .3s ease;
            }
            transition: transform .3s ease;
            label{
                color: white;
                margin-top: .7em;
                text-align: center;
                padding: 0 1em;
                @media screen and (max-width:500px){
                    padding: 0 .7em;
                }
                line-height: 1.2em;
                transition: color .3s ease;
                cursor: pointer;
            }
            &::after {
                content: " ";
                display: flex;
                position: absolute;
                top: 0;
                left: 0;
                width: 10px;
                height: 10px;
                border-bottom-right-radius: 50px;
                background-color: $mainColor;
                transition: width .3s ease;
            }
            &::before {
                content: " ";
                display: flex;
                position: absolute;
                bottom: 0;
                right: 0;
                width: 10px;
                height: 10px;
                border-top-left-radius: 50px;
                background-color: $mainColor;
                transition: width .3s ease;
            }
            &:hover{
                transform: scale(1.05);
                &::after {
                    width: 100%;
                }
                &::before {
                    width: 100%;
                }
            }
        }
        
    }

}

.dotsHolder {
    position: absolute;
    width: 500px;
    display: flex;
    flex-wrap: wrap;
    top: 5em;
    justify-content: center;
    align-items: center;
    @media screen and (max-width:500px){
        display: none  ;
    }
    span{
        width: 50px;
        height: 100px;
        background-color: $mainColor;
        border-radius: 50%;
        margin: 1em;
        filter: blur(50px);
    }
}

#serviceMinS{
    display: none;
    @media screen and (max-width:500px){
        display: flex;
    }
}

#serviceBigS{
    @media screen and (max-width:500px){
        display: none;
    }
}