@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100..700;1,100..700&display=swap");
* {
  font-family: "Josefin Sans", sans-serif;
  box-sizing: border-box;
}

body {
  margin: 0;
}

::-moz-selection { /* Code for Firefox */
  color: #2596be;
  background: #041014;
}

::selection {
  color: #2596be;
  background: #041014;
}

.prevent-select {
  -webkit-user-select: none; /* Safari */ /* IE 10 and IE 11 */
  -moz-user-select: none;
       user-select: none; /* Standard syntax */
}

.WebSiteLoader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #041014;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  animation: scaleAllAndDelte 1.5s ease 3s forwards;
}
.WebSiteLoader h1 {
  color: white;
  font-size: 5em;
  margin-bottom: 0;
  animation: fadeH1 1.5s ease forwards;
  opacity: 0;
}
@media screen and (max-width: 600px) {
  .WebSiteLoader h1 {
    font-size: 3em;
  }
}
.WebSiteLoader h2 {
  color: white;
  animation: fadeH2 1.5s ease 1.2s forwards;
  opacity: 0;
}
@media screen and (max-width: 600px) {
  .WebSiteLoader h2 {
    font-size: 1em;
  }
}

@keyframes fadeH1 {
  0% {
    opacity: 0;
    transform: translateY(15px);
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}
@keyframes fadeH2 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes scaleAllAndDelte {
  0% {
    transform: scale(1);
  }
  35% {
    transform: scale(100);
    opacity: 0;
  }
  100% {
    opacity: 0;
    display: none;
    transform: scale(400);
    filter: blur(8px);
    background-color: transparent;
    z-index: -1;
  }
}
