.menu {
  width: -moz-fit-content;
  width: fit-content;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  padding-left: 1em;
  top: 0;
  left: 0;
  z-index: 99;
}
@media screen and (max-width: 600px) {
  .menu {
    width: 100%;
    padding: 0;
  }
}
.menu div {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  flex-grow: 1;
  padding-bottom: 2em;
  animation: fade 0.5s ease forwards;
}
@media screen and (max-width: 600px) {
