}
.menu div a {
  font-size: 1em;
  text-decoration: none;
  padding: 0.7em 1em;
  width: 150px;
  text-align: center;
  margin: 0.4em 0;
  border-radius: 30px;
  background-color: transparent;
  border: 5px solid rgba(255, 255, 255, 0.825);
  color: rgba(255, 255, 255, 0.825);
  position: relative;
  display: flex;
  align-items: center;
}
@media screen and (min-width: 900px) {
  .menu div a {
    border: none;
    background-color: transparent;
    text-align: start;
    font-size: 1.2em;
    padding-left: 0;
  }
}
.menu div a:hover {
  color: #2596be;
}
.menu div a:hover::before {
  opacity: 1;
}
.menu div a::before {
  content: "";
  width: 10px;
  height: 10px;
  display: block;
  background-color: #2596be;
  margin-right: 0.5em;
  border-radius: 10px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

@keyframes fade {
  0% {
    opacity: 0;
    transform: translateY(15px);
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}
