nav {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 99;
    position: absolute;
    top: 0;
    left: 0;
    .innerNavBar{
        width: 100%;
        max-width: 1400px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 1em;
        padding-top: 2em;
        @media screen and (max-width:600px){
            padding: 1.5em 1em;
        }
        img {
            width: 260px;
            background-color: transparent;
            cursor: pointer;
            @media screen and (max-width:600px){
                width: 150px;
            }
        }
        .menuIcon {
            width: 70px;
            cursor: pointer;
            @media screen and (max-width:600px){
                width: 50px;
            }
            span{
                width: 100%;
                height: 4px;
                @media screen and (max-width:600px){
                    height: 3px;
                }
                background-color: white;
                border-radius: 2px;
                margin: .5em;
                display: block;
                transition: all .3s ease;
            }
        }
        button{
            background-color: transparent;
            color: white;
            display: flex;
            align-items: center;
            text-align: center;
            justify-content: center;
            outline: none;
            border: 4px solid white;
            padding: 1em 1.5em;
            padding-top: 1.2em;
            font-size: .95em;
            border-radius: 30px;
            cursor: pointer;
            transition: background-color .3s ease;
            @media screen and (max-width:600px){
                display: none;
            }
            &:hover {
                background-color: white;
                color: $secondColor;
            }
        }
    }
}

.logo{
    width: 100px;
}
@keyframes p-1 {
    to {margin-bottom: 8em; }
}

.heroTitle{
    z-index: 99;
    color: rgba(255, 255, 255, 0.73);
    position: absolute;
    top:50% ;
    left: 50%;
    width: 700px;
    transform: translate(-50%,-50%);
    text-align: center;
    font-size: 2.7em;
    line-height: 67px;
    
    @media screen and (max-width:600px){
        font-size: 1.5em;
        width: 350px;
        line-height: 40px;
        z-index: 1;
        
    }
    button{
        background-color: transparent;
        color: white;
        display: flex;
        align-items: center;
        text-align: center;
        justify-content: center;
        outline: none;
        border: 3px solid white;
        padding: 1em 1.5em;
        padding-top: 1.2em;
        font-size: .95em;
        border-radius: 30px;
        cursor: pointer;
        font-size: .6em;
        transition: background-color .3s ease;
        margin: 0 auto;
        margin-top: 1em;
        &:hover {
            background-color: white;
            color: $secondColor;
        }
        @media screen and (min-width:600px){
            display: none;
        }
    }
}

.Hero{
    width: 100%;
    height: 100vh;
    background-color: $secondColor;
    position: relative;
    overflow: hidden;
    animation-name: p-1;
    animation-timeline: scroll();
    animation-timing-function: linear;
    video {
        position: absolute;
        min-width: 100%;
        min-height: 100%;
        transform: translate(-50%,-50%);
        top: 50%;
        left: 50%;
        filter: brightness(.11);
    }
}

.scrollDown {
    position: absolute;
    bottom: 1em;
    left: 50%;
    transform: translateX(-50%);
    width: 50px;
    filter: brightness(.8);
    cursor: pointer;
    transition: bottom .3s ease;
    &:hover {
        bottom: .5em;
    }
}

/* CSS */
.button-55 {
  align-self: center;
  background-color: #fff;
  background-image: none;
  background-position: 0 90%;
  background-repeat: repeat no-repeat;
  background-size: 4px 3px;
  border-radius: 15px 225px 255px 15px 15px 255px 225px 15px;
  border-style: solid;
  border-width: 2px;
  box-shadow: rgba(0, 0, 0, .2) 15px 28px 25px -18px;
  box-sizing: border-box;
  color: #41403e;
  cursor: pointer;
  display: inline-block;
  font-size: 1rem;
  line-height: 23px;
  outline: none;
  padding: .75rem;
  text-decoration: none;
  transition: all 235ms ease-in-out;
  border-bottom-left-radius: 15px 255px;
  border-bottom-right-radius: 225px 15px;
  border-top-left-radius: 255px 15px;
  border-top-right-radius: 15px 225px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-55:hover {
  box-shadow: rgba(0, 0, 0, .3) 2px 8px 8px -5px;
  transform: translate3d(0, 2px, 0);
}

.button-55:focus {
  box-shadow: rgba(0, 0, 0, .3) 2px 8px 4px -6px;
}

.menu{
    width: fit-content;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    padding-left: 1em;
    top: 0;
    left: 0;
    z-index: 99;
    @media screen and (max-width:600px){
        width: 100%;
        padding: 0;
    }
    
    div {
        display: flex;
        flex-direction: column;
        align-items: start;
        justify-content: center;
        flex-grow: 1;
        padding-bottom: 2em;
        animation: fade .5s ease forwards;
        @media screen and (max-width:600px){
            background-color: $secondColor;
            height: fit-content;
            flex-grow: unset;
            border-radius: 10px;
            padding: 2em;
        }
        a {
            font-size: 1em;
            text-decoration: none;
            padding: .7em 1em;
            width: 150px;
            text-align: center;
            margin: .4em 0;
            border-radius: 30px;
            background-color: transparent;
            border: 5px solid rgba(255, 255, 255, 0.825);
            @media screen and (min-width:900px){
                border: none;
                background-color: transparent;
                text-align: start;
                font-size: 1.2em;
                padding-left: 0;
            }
            color: rgba(255, 255, 255, 0.825);
            position: relative;
            display: flex;
            align-items: center;
            &:hover {
                color: $mainColor;
                &::before{
                    opacity: 1;
                }
            }
            &::before{
                content: "";
                width: 10px;
                height: 10px;
                display: block;
                background-color: $mainColor;
                margin-right: .5em;
                border-radius: 10px;
                opacity: 0;
                transition: opacity .3s ease;
            }
        }
    }
}

@keyframes fade {
    0%{
        opacity: 0;
        transform: translateY(15px);
    }
    50%{
        opacity: 1;
    }
    100%{
        transform: translateY(0px);
        opacity: 1;
    }
}
