.MileStones{
    width: 100%;
    padding: 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 0em;
    overflow: hidden;
    &>h1{
        font-size: 4em;
    }
    .innerMileStonesHolder{
        width: 100%;
        background-color: $secondColor;
        // here
        background: rgba(12, 88, 114, 1.0);
        background: radial-gradient(at right center, rgba(12, 88, 114, 1.0), rgba(2, 26, 34, 1.0));
        border-radius: 15px;
        padding: 3em 0;
        padding-bottom: 1.5em;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        position: relative;
        @media screen and (max-width:500px){
            padding-bottom: 0;
        }
        &>h1{
            width: 100%;
            color: white;
            font-size: 7em;
            text-align: center;
            margin-bottom: .4em;
            margin-top: .6em;
            @media screen and (max-width:500px){
                font-size: 2.5em;
                margin-bottom: 0;
                margin-top: 0;
            }
        }
        .MileStone{
            width: 100%;
            padding: 2em;
            margin: 3em 0;
            @media screen and (max-width:500px){
                margin: 1em 0;
                margin-bottom: 0;
            }
            a{
                color: white;
                text-decoration: none;
                font-size: 3.5em;
                padding-left: 2em;
                position: relative;
                font-weight: 700;
                @media screen and (max-width:500px){
                    font-size: 1.5em;
                    padding-left: .5em  ;
                    display: block;
                    width: 100%;
                    text-align: center;
                }
                &:hover{
                    svg{
                        left: 60px;
                    }
                }
                svg{
                    position: absolute;
                    left: 40px;
                    top: -30px;
                    font-size: 2em;
                    transition: left .3s ease;
                    color: rgba(255, 255, 255, 0.259);
                    @media screen and (max-width:500px){
                        top: -14px;
                    }
                }
            }
            img{
                width: 100%;
                max-width: 350px;
                border-radius: 15px;
                border: 2px solid white; 
            }
            .MileStoneImages {
                display: flex;
                justify-content: center;
                align-items: start;
                flex-wrap: wrap ;
                margin-top: 4em;
                row-gap: 25px;
                column-gap: 50px;
                &>div{
                    position: relative;
                    &:hover{
                        span{
                            filter: blur(0);
                            height: 65px;
                            width: 65px;
                        }
                    }
                    img {
                        transition: filter .3s ease;
                        &:hover {
                            filter: brightness(1.2);
                        }
                    }
                    span{
                        font-size: 2.5em;
                        position: absolute;
                        top: -18px;
                        left: -17px;
                        z-index: 99;
                        color:rgba(255, 255, 255, 0.596);
                        font-weight: 600;
                        background-color: $secondColor;
                        padding: .1em .15em;
                        height: 60px;
                        width: 60px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        border-radius: 20px;
                        transition: filter .3s ease , width .2s ease , height .2s ease;
                        filter: blur(2px);
                        @media screen and (max-width:500px){
                            top: -18px;
                            left: -17px;
                        }
                    }
                }
            }
        }
    }
}

.cercelShape {
    position: absolute;
    width: 300px;
    height: 300px;
    border: 2cm solid rgba(255, 255, 255, 0.113);
    top: -3cm;
    right: -4cm;
    display: block;
    border-radius: 50%;
    
}

.cercelShape2 {
    position: absolute;
    width: 300px;
    height: 300px;
    border: 1.5cm solid rgba(255, 255, 255, 0.103);
    bottom: -4cm;
    left: -4.5cm;
    display: block;
    border-radius: 50%;
    
}