.Testimonials {
    width: 100%;
    padding: 1em;
    position: relative;
    overflow: hidden ;
    .cercelShape{
        border: 2cm solid $mainColor;
    }
    &>h1{
        width: fit-content;
        text-align: center;
        font-size: 3.5em;
        color: $secondColor;
        margin: .5em auto;
        margin-bottom: 1.3em;
        @media screen and (max-width:500px){
            margin-bottom: .5em;
            margin-top: 0.1em;
            font-size: 3em;
        }
    }
    .innerTestimonialsHolder{
        width: 100%;
        display: flex;
        justify-content: center;
        div {
            transition: all .3s ease;
            position: relative;
            &>svg{
                font-size: 3em;
            }
        }
        video{
            width: auto;
            border-radius: 20px;
            margin-bottom: .5em;
            transition: all .3s ease;
            height: 400px;
            filter: brightness(.5);
            box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
            &::before {
                content: '\25BA';
            }
            border: 3px solid $secondColor;
        }
        div:nth-child(2){
            transform: scale(1.1);
        }
        div:nth-child(1){
            transform: translateX(20px);
        }
        div:nth-child(3){
            transform: translateX(-20px);
            z-index: -1;
        }
        @media screen and (max-width:500px){
            justify-content: start;
            overflow-x: scroll;
            &::-webkit-scrollbar {
                display: none;
              }
              -ms-overflow-style: none;  /* IE and Edge */
            scrollbar-width: none;  /* Firefox */
            div{
                margin-right: .5em;

            }

            video{
                width: auto;
                border-radius: 20px;
            }
            div:nth-child(2){
                transform: scale(1);
            }
            div:nth-child(1){
                transform: none;
            }
            div:nth-child(3){
                transform: none;
                z-index: initial;
            }
        }
        @media screen and (min-width:1000px){
            &:hover{
                div:nth-child(2){
                    transform: none;
                }
                div:nth-child(1){
                    transform: none;
                }
                div:nth-child(3){
                    transform: none;
                    z-index: 1;
                }
                video{
                    margin: 0 .5em;
                    filter: brightness(.8);
                    height: 500px;
                }
            }
        }
    }
}