.Pricing{
    width: 100%;
    margin-top: 1em;
    margin-bottom: 0em;
    position: relative;
    overflow: hidden;
    & > h1 {
        font-size: 6em;
        margin-bottom: .2em;
        margin-top: 1em;
        text-align: center;
        color: $secondColor;
    }
    .bigCercleInPricing{
        width: 800px;
        height: 800px;
        border: 200px solid #2595be1e;
        position: absolute;
        top: 0em;
        left: -30em;
        z-index: -1;
        border-radius: 500px;
        
    }
    .bigCercleInPricing2{
        width: 800px;
        height: 800px;
        border: 200px solid #2595be1e;
        position: absolute;
        top: 0em;
        right: -30em;
        z-index: -1;
        border-radius: 500px;
        
    }
    .pracingCardsHolder{
        width: 100%;
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        align-items: stretch;
        column-gap: 20px;
        row-gap: 20px;
        padding: 1em;
        .pracingCard{
            &:nth-child(2){
                .pracingCardInfos{
                    font-size: 0.87em;
                }
                max-width: 360px;
            }
            &:hover{
                transform: scale(1.04);
            }
            transition: transform .3s ease;
            width: 100%;
            max-width: 350px;
            min-height: 600px;
            border-radius: 20px;
            background-color: $secondColor;
            display: flex;
            flex-direction: column;
            color: white;
            padding: 1.2em 1em;
            position: relative;
            overflow: hidden;
            h1{
                font-size: 2.1em;
                span{
                    font-size: .6em;
                }
            }
            h1 , h2{
                text-align: center;
                color: white;
            }
            h2{
                margin-top: 0;
                font-size: .9em;
                border: 2px solid white;
                padding: .5em 1em;
                border-radius: 50px;
                width: fit-content;
                margin: 0 auto;
                margin-bottom: 1em;
            }
            .pracingCardInfos{
                display: flex;
                flex-direction: column;
                padding: 0 .7em;
                margin-top: 0.8em;
                label{
                    margin-bottom: 1em;
                    span{
                        border: 2px solid $mainColor;
                        color: $mainColor;
                        display: inline-flex;
                        width: 15px;
                        height: 15px;
                        justify-content: center;
                        align-items: center;
                        border-radius: 50px;
                        padding: .7em;
                        margin-right: .5em;
                    }
                }
            }
            a{
                position: absolute;
                bottom: 1.5em;
                left: 50%;
                background-color: white;
                border: 2.5px solid white;
                text-align: center;
                text-decoration: none;
                color: $mainColor;
                padding: 1.1em 1em;
                border-radius: 50px;
                width: 85%;
                margin: 0 auto;
                display: block;
                transform: translateX(-50%);
                font-size: .9em;
                filter: brightness(.95);
            }
            .normalpricing{
                background-color: transparent;
                color: white;
                transition: background-color .3s ease;
                &:hover{
                    background-color: white;
                    color: $mainColor;
                }
            }
            box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;        
        }
    }
    
}